				console.log("治理获取到cookies值为:"+self.cookies);
		    }
		});
		uni.request({
		    url:app.globalData.ctx+ '/home/homeAirMonitoring/list', 
		    header: {
					'Content-type':'application/x-www-form-urlencoded',
