			remark:"",
			nowFileId:"",
			video:[],
			imgList:[],
			obj:{},
			cookies:"",
			pic_index:0,
			pic:[],
			preview:[],
			vde_index:0,
			vde:[],
			id:[],
			vdecss:"position: fixed; top: 0; left: 0; width: 750upx; height: 750upx; z-index: 100; margin:0;",
			big:-1,
			vdebutton:" position: fixed; bottom: -250upx;width: 100%;",
			show:1,
			previewvde:-1,
			del:[],
			wh:"",
