										},
										method: 'post',
										data: {
											"userType": e.detail.value.userType,
											"userId": e.detail.value.userId,
											"airId": e.detail.value.checkOptions,
											"homeAttachments":self.imgList
										},
										success: res => {
											console.log(res)
											uni.showToast({
											    title: '数据请求成功',
												icon:'none'
											});
										},
										fail: () => { 
											uni.showToast({
											    title: '数据请求错误',
												icon:'none'
											});
											setTimeout(function(){
												uni.hideToast();
											},1000)
										},
										complete: () => {}
									});
								}
								
							}
					    });
				}
				
				}
				
			// if(self.vde!==[]){
